<template>
  <el-aside width="210px">
    <!-- 这里el-menu定义了当前的导航菜单及属性 -->
    <el-menu
      :collapse="false"
      class="el-manager">
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-s-platform"></i>
          <span slot="title">设备管理</span>
        </template>
        <el-menu-item-group>
          <el-menu-item
            index="2"
            @click="listSwitch"
            i class="el-icon-notebook-2">
            {{"\xa0\xa0"}}设备列表
          </el-menu-item>
          <el-menu-item
            index="3"
            @click="demoSwitch"
            i class="el-icon-setting">
            {{"\xa0\xa0"}}设备优先级示例
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </el-aside>
</template>

<script>
import eventBus from "./EventBus";
export default {
  name: "container",
  data(){
    return{
    }
  },
  created() {
    this.listSwitch();
  },
  methods:{
    listSwitch(){
      eventBus.$emit('pushGatewaySwitch',"");
      eventBus.$emit('pushDemoSwitch',"display:none")
    },
    demoSwitch(){
      eventBus.$emit('pushGatewaySwitch',"display:none");
      eventBus.$emit('pushDemoSwitch',"")
    }
  }
}
</script>

<style scoped>

</style>



