<template>
  <div class="demo" id="deviceSet" :style="displayDemo">
    <div class="title">
      <h1>优先级功能说明</h1>
      <p>当有多个设备在短时间内集中访问数据库时，按照设备优先级进行排序后，按优先级从高到低的顺序依次访问数据库。</p>
    </div>
    <div class="btndemo">
      <el-button
        :loading.sync="isDemoLoading"
        type="primary "
        @click="startdemo">{{democontent}}
      </el-button>
    </div>

    <!-- demo演示的表格 -->
    <div class="demotable" v-loading="isDemoLoading">
      <el-table
        :data="visitList"
        style="width: 100%"
        v-if="visitList"
        :default-sort = "{prop: 'date', order: 'ascending'}"
      >
        <el-table-column
          prop="deviceId"
          label="设备编号"
          width="150">
        </el-table-column>
        <el-table-column
          prop="priority"
          label="设备优先级"
          sortable
          width="180">
        </el-table-column>
        <el-table-column
          prop="submittime"
          label="任务提交时间"
          sortable
          :formatter="formattersubmittime">
        </el-table-column>
        <el-table-column
          prop="executetime"
          label="访问数据库时间"
          sortable
          :formatter="formatterexetime">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import eventBus from "./EventBus";
import {BASE_API, DEMO, PORT} from "../config";
export default {
  name: "devicePriorityDemo",
  data(){
    return{
      displayDemo:"display:none",
      democontent:'开始演示',
      isDemoLoading:false,
      visitList:[],
    }
  },
  mounted() {
    eventBus.$on('pushDemoSwitch',(layoutAsideEvent)=>{
        this.displayDemo=layoutAsideEvent
      }
    )
  },
  methods:{
    async startdemo(){
      this.isDemoLoading=true;
      this.democontent="加载中";
      await axios({
        url: BASE_API+PORT[0]+DEMO,
        method: 'get',
      }).then(res => {
        this.visitList=res.data;
      })
      this.isDemoLoading=false;
      this.democontent="开始演示";
    },
    formattersubmittime(row, column) {
      let date = new Date(row["submittime"]);
      let year = date.getFullYear();
      let month = date.getMonth() < 9 ? "0" + (date.getMonth() + 1) : "" + (date.getMonth() + 1);
      let day = date.getDate() < 10 ? "0" + date.getDate() : "" + date.getDate();
      let hour = date.getHours() < 10 ? "0" + date.getHours() : "" + date.getHours();
      let minutes = date.getMinutes() < 10 ? "0" + date.getMinutes() : "" + date.getMinutes();
      let seconds = date.getSeconds() < 10 ? "0" + date.getSeconds() : "" + date.getSeconds();
      let miseconds = date.getMilliseconds() < 100 ? (date.getMilliseconds() < 10 ? (date.getMilliseconds() == 0 ? "000" + date.getSeconds() : "00" + date.getSeconds()) : "0" + date.getSeconds()) : "" + date.getMilliseconds();
      return (year + "-" + month + "-" + day + " " + hour + ":" + minutes + ":" + seconds + "." + miseconds);
    },
    formatterexetime(row, column) {
      let date = new Date(row["executetime"]);
      let year = date.getFullYear();
      let month = date.getMonth() < 9 ? "0" + (date.getMonth() + 1) : "" + (date.getMonth() + 1);
      let day = date.getDate() < 10 ? "0" + date.getDate() : "" + date.getDate();
      let hour = date.getHours() < 10 ? "0" + date.getHours() : "" + date.getHours();
      let minutes = date.getMinutes() < 10 ? "0" + date.getMinutes() : "" + date.getMinutes();
      let seconds = date.getSeconds() < 10 ? "0" + date.getSeconds() : "" + date.getSeconds();
      let miseconds = date.getMilliseconds() < 100 ? (date.getMilliseconds() < 10 ? (date.getMilliseconds() == 0 ? "000" + date.getSeconds() : "00" + date.getSeconds()) : "0" + date.getSeconds()) : "" + date.getMilliseconds();
      return (year + "-" + month + "-" + day + " " + hour + ":" + minutes + ":" + seconds + "." + miseconds);
    },
  }
}
</script>

<style lang="scss" scoped>
@import "../styles/variables.scss";

.title{
  margin-top: 20px;
  margin-left: 20px;
  margin-bottom: 20px;
}
.btndemo{
  margin-left: 20px;
  margin-bottom: 20px;
}
.demotable{

}
</style>



