<template>
  <div class="deviceList" :style=hideStyle>
    <!--        设备情况概览   -->
    <div class="profile">
      <div class="item">
        <div class="small">总设备数</div>
        <div class="strong">{{devicesNum.alldevice}}台</div>
      </div>
      <div class="item">
        <div class="small">离线设备</div>
        <div class="strong">{{devicesNum.offlinedevice}}台</div>
      </div>
      <div class="item">
        <div class="small">在线设备</div>
        <div class="strong">{{devicesNum.activedevice}}台</div>
      </div>
    </div>
    <!--    添加设备按钮      -->
    <div class="btnadd">
      <el-button type="primary" @click="addModal=true">添加设备</el-button>
    </div>
    <!--     设备列表     -->
    <div class="devicelistshow">
      <el-table
        :data="devicelist.slice(
          (slice.currentPage-1)*slice.pagesize,
          slice.currentPage*slice.pagesize)"
        :key="devicelistflush"
        :current-page.sync="slice.currentPage"
        style="width: 100%;margin: 20px"
      >
        <el-table-column type="expand" style="margin: 20px">
          <template slot-scope="scope">
            <el-form
              size="medium"
              label-position="left"
              label-width="100px">
              <el-form-item
                label="设备类型"
              >{{scope.row.deviceCategory}}
              </el-form-item>
              <el-form-item
                label="任务号"
              >{{scope.row.uuid}}
              </el-form-item>
              <el-form-item label="更多操作">
                <el-button
                  @click="handleModify(scope.$index, scope.row)"
                  size="mini"
                  type="primary">修改
                </el-button>
                <el-button
                  @click="handleStop(scope.$index, scope.row)"
                  size="mini"
                  type="warning">停止
                </el-button>
                <el-button
                  @click="handleDelete(scope.$index, scope.row)"
                  size="mini"
                  type="danger"
                >删除
                </el-button>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          prop="deviceId"
          label="设备编号"
          sortable
        >
        </el-table-column>
        <el-table-column
          prop="deviceName"
          label="设备名称"
          min-width="100px"
        >
        </el-table-column>

        <el-table-column
          prop="deviceStatus"
          label="设备状态"
          min-width="100px"
        >
        </el-table-column>
        <el-table-column
          prop="devicePriority"
          label="设备优先级"
          sortable
          min-width="100px"
        >
        </el-table-column>
<!--        <el-table-column fixed="right" label="操作" width="220px">-->
<!--          <template slot-scope= "scope">-->
<!--            &lt;!&ndash;                TODO&ndash;&gt;-->

<!--          </template>-->
<!--        </el-table-column>-->
      </el-table>
    </div>
    <!--     分页功能       -->
    <div class="pagination">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="slice.currentPage"
        :page-sizes="[5, 10, 20]"
        :page-size="slice.pagesize"
        layout="total, jumper, prev, pager, next, sizes"
        :total="devicesNum.alldevice">
      </el-pagination>
    </div>
    <!-- 添加设备的对话框 -->
    <div>
      <el-dialog
        title="添加设备"
        width="800px"
        :visible.sync="addModal"
        :before-close="handleCloseAddModal"
      >
        <div class="form">

          <el-alert title="特别说明" description="这里放置说明内容" type="warning" show-icon></el-alert>
          <el-form
            :inline="true"
            :model="form"
            class="demo-form-inline"
            label-width="80px"
            ref="form"
          >
            <div>
              <el-form-item
                label="设备类型"
                prop="value"
                :rules="[  { required: true, message: '不能为空'}, ]"
              >
                <el-select v-model="form.value" placeholder="设备类型">
                  <el-option
                    v-for="item in form.deviceCategory"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="设备编号" prop="name">
                <el-input v-model="form.deviceId" placeholder="设备编号" type="number"></el-input>
              </el-form-item>
            </div>
            <div>
              <el-form-item label="设备名称" prop="name">
                <el-input v-model="form.deviceName" placeholder="设备名称"></el-input>
              </el-form-item>
              <el-form-item label="优先级" prop="devicePriority">
                <el-input-number v-model="form.devicePriority" placeholder="输入1~10"  max:10 min:1></el-input-number>
              </el-form-item>
            </div>
          </el-form>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="sureAddModal">确定</el-button>
          <el-button @click="addModal = false">取消</el-button>
        </div>
      </el-dialog>
    </div>
    <!-- 修改设备的对话框 -->
    <div>
      <el-dialog
        title="修改设备"
        width="800px"
        :visible.sync="modifyModal"
        :before-close="handleClosemodifyModal"
      >
        <div class="form">

          <el-form
            :inline="true"
            :model="form"
            class="demo-form-inline"
            label-width="80px"
            ref="form"
          >
            <div>
              <el-form-item
                label="设备类型"
                prop="value"
              >
                <el-select v-model="form.value" placeholder="$(form.category)" disabled >
                  <el-option
                    v-for="item in form.deviceCategory"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="设备编号" prop="code">
                <el-input v-model="form.deviceId" placeholder="设备编号" type="number"></el-input>
              </el-form-item>
            </div>
            <div>
              <el-form-item label="设备名称" prop="name">
                <el-input v-model="form.deviceName" placeholder="设备名称"></el-input>
              </el-form-item>
              <el-form-item label="优先级" prop="priority">
                <el-input-number v-model="form.devicePriority" placeholder="输入1~10"  max:10 min:1></el-input-number>
              </el-form-item>
            </div>
          </el-form>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="sureModifyModal">确定</el-button>
          <el-button @click="modifyModal = false">取消</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import eventBus from "./EventBus";
import {ADD_MODAL, BASE_API, DELETE_MODAL, DEVICE_LIST, MODIFY_MODAL, STOP_MODAL} from "../config";
export default {
  name: "deviceList",
  props:[
    "ofport",
    "hideStyle"
  ],
  data(){
    return{
      displayList: "",
      addModal:false,
      modifyModal: false,
      form: {//添加设备对话框
        deviceId: 0,
        uuid: '',
        deviceName:'',
        devicePriority: 0,
        deviceCategory: [
          {
            value: 0,
            label: 'Example'
          }, {
            value: 1,
            label: 'Sensor'
          }, {
            value: 2,
            label: 'Camera'
          }
        ],
        value:0,
        category: '',
        search:''
      },
      devicesNum:{//设备概览
        alldevice:0,
        offlinedevice:0,
        activedevice:0
      },
      slice:{//分页设置
        currentPage:1,
        pagesize:5,
        listcount:0,
      },
      devicelist: [],
      devicelistflush:0,
      newDevice: '',
    }
  },
  created() {
    this.flushData();
  },
  mounted() {
    // 通过事件总线监听消息
    eventBus.$on('pushListSwitch', (layoutAsideEvent) => {
      this.displayList = layoutAsideEvent
    })
  },
  methods:{
    handleSizeChange(val) {
      this.slice.pagesize=val;
    },
    handleCurrentChange(val) {
      this.slice.currentPage = val;
    },
    handleCloseAddModal() {
      this.addModal=false;
    },
    handleClosemodifyModal(){
      this.modifyModal=false;
    },
    handleModify(index,row){
      this.form["deviceId"]=row["deviceId"];
      this.form["devicePriority"]=row["devicePriority"];
      this.form["deviceName"]=row["deviceName"];
      this.form["uuid"]=row["uuid"];
      this.form["category"]=row["category"];
      this.modifyModal=true;
    },
    handleStop(index,row){
      let uuidvar = row["uuid"];
      axios({
        url:BASE_API+this.ofport+STOP_MODAL,
        method:'post',
        data: uuidvar,
        headers: {
          'Content-Type': 'text/plain'
        },
      }).then(result => {
      });
      this.flushData();
    },
    handleDelete(index,row){
      let uuidvar = row["uuid"];
      axios({
        url:BASE_API+this.ofport+DELETE_MODAL,
        method:'post',
        data: uuidvar,
        headers: {
          'Content-Type': 'text/plain'
        },
      }).then(result => {
          this.flushData();
        }
      )
    },
    async sureAddModal() {
      await axios({
        url:BASE_API+this.ofport+ADD_MODAL,
        method:'post',
        contentType:"application/json",
        data:{
          deviceId:this.form.deviceId,
          deviceName:this.form.deviceName,
          devicePriority:this.form.devicePriority,
          deviceCategory:this.form.deviceCategory[this.form.value].label,
        },
      }).then(res=>{
        this.flushData();
        this.addModal=false;
      })
    },
    async sureModifyModal(){
      await axios({
        url:BASE_API+this.ofport+MODIFY_MODAL,
        method:'post',
        contentType:"application/json",
        data:{
          deviceId:this.form.deviceId,
          deviceName:this.form.deviceName,
          devicePriority:this.form.devicePriority,
          deviceCategory:'',
          uuid:this.form.uuid
        },
      }).then(res=>{
        this.flushData();
        this.modifyModal=false;
      })
    },
    flushData: async function () {
      await axios({
        url: BASE_API+this.ofport+DEVICE_LIST,
        method: 'post',
      }).then(res => {
        this.devicesNum.alldevice=0;
        let temp = 0;
        this.devicelist=[];
        for (let i in res.data) {
          this.devicelist.push(res.data[i]);
          this.devicesNum.alldevice++;
          if((res.data[i])["deviceStatus"]=="offline")
            temp++;
        }
        this.devicesNum.offlinedevice=temp;
        this.listcount=this.devicesNum.alldevice;
        this.devicesNum.activedevice=this.devicesNum.alldevice-this.devicesNum.offlinedevice;
      });
      this.devicelistflush++;
      this.$parent.$parent.$parent.flushGateway();
    },
  }

}
</script>

<style lang="scss" scoped>

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
  font-size: 25px;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}

  .form {
  background: #fff;

  .demo-form-inline {
    width: 700px;
    margin: auto;
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
  .btnadd{
    margin-left: 20px;
    margin-bottom: 20px;
  }
  .pagination {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .profile {
    display: flex;
    justify-content: space-around;
    margin-bottom: 40px;
    border-bottom: 1px solid #ddd;
    padding-bottom: 20px;

    .item {
      width: 100%;
      text-align: center;
      margin-top: 20px;
      border-right: 1px solid #ddd;

      .small {
      }

      .strong {
        margin-top: 10px;
        font-size: 20px;
      }

      &:last-child {
        border-right: none;
      }
    }
  }
</style>



