import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/device/index'
    },
    {
      path: '/device/index',
      component: index,
      name: 'index',
    }
  ]
})



