<template>
  <div class="jump2list" id="gatewayView" :style="displayGateway" v-loading="loading">
    <!--  选择网关  -->
    <el-row   >
      <el-col v-for="(Gateway,index) in gatewayGroup" :key="index">
        <el-card :body-style="{ padding: '0px' }">
          <img src="../../static/gatewayImage.png" class="image" alt="">
          <div style="padding: 14px;"  :key="gatewayKey">
            <span>{{Gateway["gatewayName"]}}（{{Gateway["gatewayIP"]}}）</span>
            <el-progress
              class="progress-bar"
              :stroke-width="26"
              :percentage="100*Gateway['activeDevice'] / Gateway['allDevice']"
              :width="25"
              :show-text="false"
            ></el-progress>
            <span>总设备{{Gateway['allDevice']}}台，在线设备{{Gateway['activeDevice']}}台</span>
            <div class="bottom clearfix">
              <el-button
                type="text"
                class="detailbutton"
                @click=""
              >网关详情</el-button>
              <el-button
                type="text"
                class="collapseButton"
                @click="handleDisplay(index)"
                :key="showKey"
              >{{collapseButtonContent[index]}}</el-button>
            </div>
          </div>
        </el-card>
        <el-collapse-transition>
        <deviceList :ofport="ofPORTS[index]" v-show="listShow[index]==true"></deviceList>
        </el-collapse-transition>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import eventBus from "./EventBus";
import axios from "axios";
import {BASE_API,PORT,GATEWAY_LIST} from "../config";
import deviceList from "./deviceList";
export default {
  name: "gatewayView",
  components:{
    deviceList
  },
  data(){
    return{
      gatewayKey:999999,
      loading:true,
      showKey:0,
      listShow:[],
      collapseButtonContent:[],
      gatewayGroup:[],
      displayGateway:"",
      ofPORTS:PORT,
    }
  },
  created() {
    this.flushGateway();
    this.initialListShow()
  },
  mounted() {
    // 通过事件总线监听消息
    eventBus.$on('pushGatewaySwitch', (layoutAsideEvent) => {
      this.displayGateway = layoutAsideEvent
    })
  },
  watch:{

  },
  methods:{
    initialListShow:function (){
      for (let i = 0; i < PORT.length; i++) {
        this.listShow[i]=false;
        this.collapseButtonContent[i]="展开"
      }
    },
    handleDisplay (index) {
      this.listShow[index]=!this.listShow[index];
      this.collapseButtonContent[index]=this.listShow[index]?"折叠":"展开";
      this.showKey++;
    },
    async flushGateway(){
      for (let i = 0; i < PORT.length; i++) {
        await axios({
          url: BASE_API+PORT[i]+GATEWAY_LIST,
          method: 'post',
        }).then(res=>{
          this.gatewayGroup[i]=res.data;
        })
      }
      this.loading=false;
      this.gatewayKey--;
    }
  }
}
</script>

<style lang="scss" scoped>

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
    clear: both
  }
  .bottom {
    margin-top: 13px;
    line-height: 12px;
    padding: 0;
    float: right;
  }
  .progress-bar{
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .image {
    float: left;
    display: block;
    border: 1px;
    margin: 20px;
  }
</style>



